import {fireEvent, render, waitFor} from '@testing-library/react-native';
import 'react-native-get-random-values';
import {v4 as uuidv4} from 'uuid';
import Adapter from 'enzyme-adapter-react-16';
import {mount, shallow} from 'enzyme';
import * as enzyme from 'enzyme';
import Base from '../../Apps/Base';
import {loadFeature, defineFeature} from 'jest-cucumber';
import {QuizContext} from './../../Apps/QuizContext';
import {View, TextInput} from 'react-native';
const uuid = uuidv4();
const feature = loadFeature('./features/base.feature');
enzyme.configure({adapter: new Adapter()});
defineFeature(feature, test => {
  const mockQuizContext = {
    score: 7,
    setScore: jest.fn(),
    name: 'chandu',
    setName: jest.fn(),
    email: 'chandra@chandu.com',
    setMail: jest.fn(),
    phone: '1234567890',
    setPhone: jest.fn(),
    selectedValue: 'ReactJS',
    setSelected: jest.fn(),
    trackScore: true,
    setTracker: jest.fn(),
  };

  test('User submits form with invalid email', ({given, when, and, then}) => {
    let wrapper: any;
    beforeEach(() => {
      wrapper = shallow(<Base />);
    });
    given('I am on the base screen', () => {
      const wrapper = shallow(<Base />);
      expect(wrapper).toMatchSnapshot();
    });
    when('When I enter a valid email "test@test.com"', () => {
      const emailInput = wrapper.find('[testID="emailInput"]');
      emailInput.simulate('change', {target: {value: 'test@test.com'}});
    });
    and('I enter a valid name "John"', () => {
      const nameInput = wrapper.find('[testID="nameInput"]');
      nameInput.simulate('change', {target: {value: 'John'}});
    });
    and('I enter a valid phone number "1234567890"', () => {
      const phoneInput = wrapper.find('[testID="phoneInput"]');
      phoneInput.simulate('change', {target: {value: '1234567890'}});
    });

    and('I select "ReactJS" as my preferred language', () => {
      const reactJsRadioButton = wrapper.find('[testID="reactJsRadioButton"]');
      reactJsRadioButton.simulate('change', {target: {value: 'ReactJS'}});
    });

    and('I select "No" to track my score', () => {
      const noRadioButton = wrapper.find('[testID="noRadioButton"]');
      noRadioButton.simulate('change', {target: {value: 'false'}});
    });

    and('I submit the form', () => {
      const submitButton = wrapper.find('[testID="submitButton"]');
      submitButton.simulate('click');
    });

    then('I should see the start quiz button', () => {
      const startQuizButton = wrapper.find('[testID="startQuizButton"]');
      expect(startQuizButton.exists()).toBe(true);
    });
  });
});
